"use client";
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, PieChart, Pie, Cell, BarChart, Bar } from "recharts";

interface AnalyticsData {
  monthlyExpenses: Array<{ month: string; amount: number }>;
  categoryBreakdown: Array<{ name: string; value: number; color: string }>;
  supplierComparison: Array<{ supplier: string; orders: number; amount: number }>;
  weeklyTrends: Array<{ day: string; orders: number }>;
}

export default function VendorAnalytics() {
  const router = useRouter();
  const [analyticsData, setAnalyticsData] = useState<AnalyticsData | null>(null);
  const [loading, setLoading] = useState(true);
  const [selectedPeriod, setSelectedPeriod] = useState("6months");

  useEffect(() => {
    // Check authentication
    const token = localStorage.getItem("token");
    const role = localStorage.getItem("role");
    
    if (!token || role !== "vendor") {
      router.push("/auth/vendor-login");
      return;
    }

    // Mock analytics data
    const mockData: AnalyticsData = {
      monthlyExpenses: [
        { month: "Jul", amount: 2400 },
        { month: "Aug", amount: 3200 },
        { month: "Sep", amount: 2800 },
        { month: "Oct", amount: 3600 },
        { month: "Nov", amount: 4200 },
        { month: "Dec", amount: 3800 }
      ],
      categoryBreakdown: [
        { name: "Spices", value: 35, color: "#FF6B35" },
        { name: "Vegetables", value: 25, color: "#4ECDC4" },
        { name: "Grains", value: 20, color: "#45B7D1" },
        { name: "Dairy", value: 12, color: "#96CEB4" },
        { name: "Others", value: 8, color: "#FFEAA7" }
      ],
      supplierComparison: [
        { supplier: "Sharma Spices", orders: 24, amount: 8400 },
        { supplier: "Mumbai Vegetables", orders: 18, amount: 5200 },
        { supplier: "Delhi Grains", orders: 15, amount: 4800 },
        { supplier: "Pune Fresh", orders: 12, amount: 3600 },
        { supplier: "Gujarat Spices", orders: 8, amount: 2800 }
      ],
      weeklyTrends: [
        { day: "Mon", orders: 8 },
        { day: "Tue", orders: 12 },
        { day: "Wed", orders: 15 },
        { day: "Thu", orders: 18 },
        { day: "Fri", orders: 22 },
        { day: "Sat", orders: 25 },
        { day: "Sun", orders: 10 }
      ]
    };

    setAnalyticsData(mockData);
    setLoading(false);
  }, [router]);

  const getTotalExpenses = () => {
    if (!analyticsData) return 0;
    return analyticsData.monthlyExpenses.reduce((sum, item) => sum + item.amount, 0);
  };

  const getAverageMonthlyExpense = () => {
    if (!analyticsData) return 0;
    return Math.round(getTotalExpenses() / analyticsData.monthlyExpenses.length);
  };

  const getTotalOrders = () => {
    if (!analyticsData) return 0;
    return analyticsData.supplierComparison.reduce((sum, item) => sum + item.orders, 0);
  };

  const getTopSupplier = () => {
    if (!analyticsData || analyticsData.supplierComparison.length === 0) return "N/A";
    return analyticsData.supplierComparison[0].supplier;
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-orange-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-12 h-12 mx-auto border-4 border-orange-500 border-t-transparent rounded-full animate-spin mb-4"></div>
          <p className="text-gray-600">Loading analytics...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-orange-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center space-x-4">
              <Link href="/vendor/dashboard" className="text-2xl font-bold text-orange-600 font-baloo">
                BazaarBridge
              </Link>
              <span className="text-gray-400">|</span>
              <span className="text-gray-600">Analytics</span>
            </div>
            <div className="flex items-center space-x-4">
              <select
                value={selectedPeriod}
                onChange={(e) => setSelectedPeriod(e.target.value)}
                className="px-3 py-1 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-orange-500 focus:border-transparent"
              >
                <option value="3months">Last 3 Months</option>
                <option value="6months">Last 6 Months</option>
                <option value="1year">Last Year</option>
              </select>
            </div>
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Page Title */}
        <div className="mb-8">
          <h2 className="text-3xl font-bold text-gray-800 font-baloo mb-2">Analytics Dashboard üìä</h2>
          <p className="text-gray-600">Track your spending patterns and business insights</p>
        </div>

        {/* Key Metrics */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <div className="bg-white rounded-lg p-6 shadow-sm">
            <div className="flex items-center">
              <div className="text-3xl mr-4">üí∞</div>
              <div>
                <p className="text-sm text-gray-600">Total Expenses</p>
                <p className="text-2xl font-bold text-gray-800">‚Çπ{getTotalExpenses().toLocaleString()}</p>
              </div>
            </div>
          </div>
          <div className="bg-white rounded-lg p-6 shadow-sm">
            <div className="flex items-center">
              <div className="text-3xl mr-4">üìà</div>
              <div>
                <p className="text-sm text-gray-600">Avg Monthly</p>
                <p className="text-2xl font-bold text-gray-800">‚Çπ{getAverageMonthlyExpense().toLocaleString()}</p>
              </div>
            </div>
          </div>
          <div className="bg-white rounded-lg p-6 shadow-sm">
            <div className="flex items-center">
              <div className="text-3xl mr-4">üì¶</div>
              <div>
                <p className="text-sm text-gray-600">Total Orders</p>
                <p className="text-2xl font-bold text-gray-800">{getTotalOrders()}</p>
              </div>
            </div>
          </div>
          <div className="bg-white rounded-lg p-6 shadow-sm">
            <div className="flex items-center">
              <div className="text-3xl mr-4">üèÜ</div>
              <div>
                <p className="text-sm text-gray-600">Top Supplier</p>
                <p className="text-lg font-bold text-gray-800">{getTopSupplier()}</p>
              </div>
            </div>
          </div>
        </div>

        {/* Charts Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-8">
          {/* Monthly Expenses Line Chart */}
          <div className="bg-white rounded-lg p-6 shadow-sm">
            <h3 className="text-lg font-semibold text-gray-800 mb-4">Monthly Expenses Trend</h3>
            <ResponsiveContainer width="100%" height={300}>
              <LineChart data={analyticsData?.monthlyExpenses}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="month" />
                <YAxis />
                <Tooltip formatter={(value) => [`‚Çπ${value}`, 'Amount']} />
                <Line 
                  type="monotone" 
                  dataKey="amount" 
                  stroke="#FF6B35" 
                  strokeWidth={3}
                  dot={{ fill: '#FF6B35', strokeWidth: 2, r: 6 }}
                />
              </LineChart>
            </ResponsiveContainer>
          </div>

          {/* Category Breakdown Pie Chart */}
          <div className="bg-white rounded-lg p-6 shadow-sm">
            <h3 className="text-lg font-semibold text-gray-800 mb-4">Expense by Category</h3>
            <ResponsiveContainer width="100%" height={300}>
              <PieChart>
                <Pie
                  data={analyticsData?.categoryBreakdown}
                  cx="50%"
                  cy="50%"
                  outerRadius={100}
                  fill="#8884d8"
                  dataKey="value"
                  label={({ name, value }) => `${name}: ${value}%`}
                >
                  {analyticsData?.categoryBreakdown.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.color} />
                  ))}
                </Pie>
                <Tooltip formatter={(value) => [`${value}%`, 'Percentage']} />
              </PieChart>
            </ResponsiveContainer>
          </div>
        </div>

        {/* Additional Charts */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Supplier Comparison Bar Chart */}
          <div className="bg-white rounded-lg p-6 shadow-sm">
            <h3 className="text-lg font-semibold text-gray-800 mb-4">Top Suppliers by Orders</h3>
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={analyticsData?.supplierComparison}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis 
                  dataKey="supplier" 
                  angle={-45}
                  textAnchor="end"
                  height={80}
                  fontSize={12}
                />
                <YAxis />
                <Tooltip formatter={(value, name) => [value, name === 'orders' ? 'Orders' : 'Amount (‚Çπ)']} />
                <Bar dataKey="orders" fill="#4ECDC4" />
              </BarChart>
            </ResponsiveContainer>
          </div>

          {/* Weekly Order Trends */}
          <div className="bg-white rounded-lg p-6 shadow-sm">
            <h3 className="text-lg font-semibold text-gray-800 mb-4">Weekly Order Pattern</h3>
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={analyticsData?.weeklyTrends}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="day" />
                <YAxis />
                <Tooltip formatter={(value) => [value, 'Orders']} />
                <Bar dataKey="orders" fill="#45B7D1" />
              </BarChart>
            </ResponsiveContainer>
          </div>
        </div>

        {/* Insights Section */}
        <div className="mt-8 bg-gradient-to-r from-orange-100 to-yellow-100 rounded-lg p-6">
          <h3 className="text-lg font-semibold text-gray-800 mb-4">üìà Key Insights</h3>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="bg-white rounded-lg p-4">
              <h4 className="font-medium text-gray-800 mb-2">Spending Pattern</h4>
              <p className="text-sm text-gray-600">
                Your highest spending month was November with ‚Çπ4,200. Consider bulk purchasing during low-demand periods.
              </p>
            </div>
            <div className="bg-white rounded-lg p-4">
              <h4 className="font-medium text-gray-800 mb-2">Supplier Loyalty</h4>
              <p className="text-sm text-gray-600">
                You order most frequently on Fridays and Saturdays. Sharma Spices is your most reliable supplier.
              </p>
            </div>
            <div className="bg-white rounded-lg p-4">
              <h4 className="font-medium text-gray-800 mb-2">Cost Optimization</h4>
              <p className="text-sm text-gray-600">
                Spices account for 35% of your expenses. Consider negotiating bulk rates with spice suppliers.
              </p>
            </div>
            <div className="bg-white rounded-lg p-4">
              <h4 className="font-medium text-gray-800 mb-2">Growth Opportunity</h4>
              <p className="text-sm text-gray-600">
                Your order frequency is increasing. Consider exploring new suppliers for better rates.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
